/*Spells*/

.Cont-Spells
{
  width: 100%;
  height: fit-content;
  min-height: 100vh;
  /*background-image: url("images/spells_fondo.webp");*/
  /*background-image: url("images/spells_fondo2.png");*/
  background:
  linear-gradient(45deg, #343702 0%, #184500 20%, #187546 30%, #006782 40%, #0b1284 50%, #760ea1 60%, #83096e 70%, #840b2a 80%, #b13e12 90%, #e27412 100%);
  background-size: 470px 470px, 970px 970px, 410px 410px, 610px 610px, 530px 530px, 730px 730px, 100% 100%;
  background-color: #840b2a;  background-size:cover;
  background-position-x: center;
  background-position-y: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.Cont-Spells-Cards
{
  width: 80%;
  height: 100%;
  min-height: 30vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  border: 1px solid rgba( 23, 8, 90, 0.3 );
  border-radius: 20px;
  background: rgba( 23, 8, 90, 0.3 );
  margin-bottom: 100px;
}

.Cont-Spells-SpellCard
{
  width: 300px;
  margin: 50px;
  height: fit-content;
  border-radius: 10px;
  border: 1px solid wheat;
  text-align: center;
  font-weight: 900;
  font-size: 20px;
  cursor: pointer;
}
.Cont-Spells-SpellCard-Oculto
{
  display: none;
  width: 100%;
  background-color: azure;
  padding-bottom: 30px;
  padding-top: 30px;
  border-radius: 0px 0px 5px 5px;
  color:#040607 !important;
  border-top: 1px solid black;
}
.Cont-Spells-SpellCard-Text
{
  margin: 15px;
}
.Cont-Spells-SpellCard-Text div
{
  font-weight: 100 !important;
}

.Spell-Tipo
{
  margin: 0px 10px 10px 10px !important;
}

.Cont-Spells-Filter
{
  width: 60%;
  height: 100px;
  display: flex;
  align-content: center;
  justify-content: center;
  align-items: center;
  border: 1px solid rgba( 23, 8, 90, 0.3 );
  border-radius: 20px;
  background: rgba( 23, 8, 90, 0.3 );
  margin: 50px;
}

.Cont-Spell-Filter-Movil-Principal
{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.Cont-Spell-Filter-Movil-Principal-Text
{
  width: 80%;
  text-align: center;
  font-size: 30px; 
  display: none;
}

.Cont-Spell-Filter-Movil-Principal-Flecha
