/*Flecha Atras*/
.FlechaAnterior
{
  width: 100px;
  height: 100px;
  font-size: 90px;
  padding-right: 10px;
  top: 10px;
  left: 20px;
  text-align: center;
  position: absolute;
  cursor:pointer;
  color:rgb(9, 9, 9);
  background: rgba(217, 218, 214, 0.8);
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
  backdrop-filter: blur( 4px );
  -webkit-backdrop-filter: blur( 4px );
  border-radius: 10px;
  border: 1px solid rgba(23, 22, 22, 0.18);
  border-radius: 100%;
}

.inputStyle
{
  width: 200px;
  background-color: transparent;
  color: whitesmoke;
  border: 0px solid white;
  border-bottom: 1px solid whitesmoke;
  margin-right: 10px;
  cursor: text;
}

.inputStyle::placeholder
{
  color:whitesmoke;
}

.inputStyle:hover::placeholder
{
  color:rgb(130, 160, 216);
  border: 0px solid white;
  border-bottom: 1px solid rgb(130, 160, 216);
}
.inputStyle:hover
{
  color:rgb(130, 160, 216);
  border: 0px solid white;
  border-bottom: 1px solid rgb(130, 160, 216);
}



.inputStyle:focus
{
  border: 0px solid wheat !important;
}

.botonStyle
{
  width: 50px;
  background-color: transparent;
  color: whitesmoke;
  border: 0px solid;
  font-size: 20px;
  margin-right: 20px;

}

.botonStyle:hover
{
  color:red;
  font-size: 30px;

}

.botonStyle:active
{
  color: rgb(130, 160, 216);
  font-size: 35px;

}

